.Main {
  height: 100vh;
  width: 100vw;
  /* background: url('../assets/building-md.jpg') no-repeat; */
  background: rgb(0, 0, 0);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Main:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(81, 77, 55, 1) 54%,
    rgba(181, 172, 124, 1) 81%,
    rgba(233, 211, 97, 1) 100%,
    rgba(255, 255, 255, 1) 100%
  );
  opacity: 0.8;
  z-index: 1;
}

.Main > * {
  z-index: 100;
}

.title {
  height: 50vh;
  display: flex;
}

.cards {
  height: 50vh;
  width: 90vw;
}

@media (max-width: 1280px) {
  .Main {
    height: 100vh;
  }
  .title {
    height: 40vh;
  }
  .cards {
    height: 60vh;
  }
}

@media (max-width: 960px) {
  .Main {
    height: 100vh;
  }
  .title {
    height: 30vh;
  }
  .cards {
    height: 70vh;
  }
}

@media (max-width: 599px) {
  .Main {
    background: #000000;
    height: '600px';
  }
  .title {
    height: 10vh;
  }
  .cards {
    height: 90vh;
    width: 98vw;
    overflow-y: auto;
  }
}
